.wrapper {
  background-color: white;
  width: 400px;
  padding: 2rem;
  box-shadow: 0 1.5rem 1rem -1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.3rem;
}

.input {
  width: 100%;
  height: 45px;
  padding: 0 1rem;
  margin-top: 1rem;
  box-sizing: border-box;
  font: inherit;
  border-radius: 0.2rem;
  border: 2px solid #d4d5d6;
  color: #565656;
  -webkit-appearance: none;
}

.input:focus {
  border-color: cornflowerblue;
  outline: none;
}

.input.has-error {
  border-color: tomato;
}

.error {
  margin: 0;
  font-size: 90%;
  color: tomato;
}

.tag-item {
  background-color: #d4d5d6;
  display: inline-block;
  font-size: 14px;
  border-radius: 30px;
  height: 30px;
  padding: 0 4px 0 1rem;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 0.3rem 0.3rem 0;
}

.tag-item > .button {
  background-color: white;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  font: inherit;
  margin-left: 10px;
  font-weight: bold;
  padding: 0;
  line-height: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.mymodal {
  border-radius: 30px;
  border: 1px solid #3EBDF1;
  width: 40%;
  height: 45%;
  
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #fff;
  overflow: auto;
  outline: none;
  padding: 20px;
  position: relative;
}

.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color:#a8a8a88a ;
}

.dayclass {
  border: 1px #3ebdf1 solid;
  border-radius: 10px;
  /* // box-shadow: 6px 6px 15px rgb(0 0 0 / 15%); */
  /* margin: 1rem; */
  height: 2.5rem;
  font-size: 1rem;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  line-height: 2.5rem;
  text-align: center;
  color: #3ebdf1;
  padding: .25rem .5rem;
  background: transparent;
  
}
#cntbtn{
   background-color:#3ebdf1 ;
   color: cornsilk;
   margin-top: .6rem;
   
 }
 #ssbtn{
